.footer__soc-list {
    display: flex;
}

.footer__soc-item {
    cursor: pointer;
    width: 45px;
    height: 45px;
    border: solid var(--color-white) 2px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__soc-link {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__svg {
    transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__soc-item:hover .footer__svg {
    fill:var(--accent-color);
}

.footer__soc-list li:not(:last-child) {
    margin-right: 14px;
}

.footer__soc-item:hover {
    box-shadow: -1px -1px 49px 3px rgba(77,194,116,0.75);
    border: var(--accent-color) solid 2px;
    fill: var(--accent-color);
    transition: 500ms cubic-bezier(0.4, 0, 0.2, 1); 
}

.footer__svg {
    fill: var(--color-grey);
}

.footer__address-wrapper {
    margin-top: 40px;
}


.footer__navigation-link {
    padding: 14px;
    transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__navigation-link:hover {
    color: var(--accent-color);
    transition: 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__address-link:hover .footer__svg  {
    fill: var(--accent-color);
}

.footer__address-link:hover {
    color: var(--accent-color);
}


.footer__address-list li:not(:last-child) {
    margin-bottom: 20px;
}

.footer__rights {
    border-top: solid var(--color-green-pseudo) 1px;
    padding: 40px 0;
    text-align: center;
}
